.main{
    height: 100%;
    display: flex;
    align-items: center;
    flex-direction: column;
    padding-top: 20px ;
}

.modal{
    display: none;
    z-index: 5;
    top: 0;
    left: 0;
    position: fixed;
    height: 100vh;
    width: 100vw;
    background-color: rgba(56, 87, 116, .4);

    transition: .2s;
}
.modal.show{
    display: block;
}
.modal__inner{
    display: flex;
    height: 100vh;
    align-items: center;
    justify-content: center;
}
.modalButton{
    display: flex;
    justify-content: center;
    align-items: center;

    position: fixed;
    z-index: 6;
    bottom: 15px;
    right: 15px;
    height: 50px;
    width: 50px;
    background-color: #385774;
    color: #fff;
    font-size: 40px;
    text-align: center;
    border-radius: 50%;
}
.form{
    width: 460px;
    margin-bottom: 15px;
}
.input{
    width: 100%;
    height: 49px;
    background: #E5E5E5;
    border-radius: 15px;
    padding: 10px 20px;
    color: #000;
    font-size: 25px;
    margin-bottom: 10px;
}
.button{
    width: 50%;
    border-radius: 15px;
    height: 25px;
    background: #E5E5E5;
    font-size: 16px;
}

@media (max-width:600px){
    .form{
        width: 290px;
    }
    
}